<template>
  <div class="container">
		<div class="box">
			<el-input
				v-model="tipText"
				:autosize="{ minRows: 8 }"
				type="textarea"
				placeholder="提示词"
			/>
			<el-button round class="copy-btn" @click="copy">复制</el-button>
		</div>

		<el-form :inline="true" :model="searchText" class="demo-tabs">
			<el-form-item label="翻译">
				<el-input v-model="searchText.text" placeholder="搜索文字" clearable/>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="complete">添加</el-button>
			</el-form-item>
		</el-form>

		<el-tabs
			v-model="activeName"
			type="card"
			class="demo-tabs"
			@tab-click="handleClick"
		>
			<div v-for="(item, index) in categoryList" :key="index">
				<el-tab-pane :label="item.name" :name="index">
					<el-checkbox-group v-model="checkList">
						<div v-for="(nth, nthIndex) in item.child" :key="nthIndex" @change="changeItem">
							<el-checkbox :label="nth.label"/>
						</div>
					</el-checkbox-group>
				</el-tab-pane>
			</div>
		</el-tabs>
  </div>
</template>

<script setup>
import {ref, reactive} from 'vue';
import data from '@/utils/base.js';
import useClipboard from "vue-clipboard3";
const { toClipboard } = useClipboard();
import { ElMessage } from 'element-plus';
import {getCurrentInstance} from 'vue';
import md5 from 'js-md5';

const { proxy } = getCurrentInstance();
console.log('proxy', proxy);
	
const tipText = ref('');
const categoryList = ref(data.CATEGORY_LIST);
const checkList = ref([]);
const activeName = ref(0);

const searchText = reactive({
  text: ''
})

const changeItem = () => {
	const chilAllList = [];
	// 汇总所有枚举数据
	data.CATEGORY_LIST.map(item => {
		chilAllList.push(...item.child);
	})

	const newCheck = checkList.value;
	const textArray = [];
	
	for(let i = 0; i < chilAllList.length; i++) {
		for(let j = 0; j < newCheck.length; j++) {
			if(chilAllList[i].label === newCheck[j]) {
				textArray.push(chilAllList[i].value);
			}
		}
	}
	
	let text = textArray.join(',');
	tipText.value = text;
}

const copy = async () => {
	await toClipboard(tipText.value);
	ElMessage({
    message: '复制成功！',
    type: 'success',
  })
}

const complete = () => {
	const appid = '20230319001606247';
	const key = '6JVWNdj5GueA8zJEQAXa';
	const salt = (new Date).getTime();
	const q = searchText.text;
	
	const sign = md5(`${appid}${q}${salt}${key}`);
	const from = 'zh';
	const to = 'en';
	jsonp({
		url: 'https://fanyi-api.baidu.com/api/trans/vip/translate',
		data: {
			q,
			from,
			to,
			appid,
			salt,
			sign
		},
		callback: function(ret){
			console.log('ret==', ret)
			let list = categoryList.value[activeName.value].child;
			list.push({label: ret.trans_result[0].src, value: ret.trans_result[0].dst});
		}
	})
}

const jsonp = (setting) => {
  setting.data = setting.data || {}
  setting.key = setting.key||'callback'
  setting.callback = setting.callback||function(){} 
  setting.data[setting.key] = '__onGetData__'

  window.__onGetData__ = function(data){
    setting.callback (data);
  }

  var script = document.createElement('script')
  var query = []
  for(var key in setting.data){
    query.push( key + '='+ encodeURIComponent(setting.data[key]) )
  }
  script.src = setting.url + '?' + query.join('&')
  document.head.appendChild(script)
  document.head.removeChild(script)
}


</script>

<style scoped>
.container {
	padding: 20px;
}
.box {
	position: relative;
}
.copy-btn {
	position: absolute;
	bottom: 10px;
	right: 10px;
}
.demo-tabs {
	margin-top: 50px;
}
</style>